import React, { Component } from 'react';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import UserImageComponent from './UserImageComponent';
import './UpperNavigation.css';
class UpperNavigation extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div className="upper-navigation">
                <Navbar expand="lg" className="upper-navbar">
                    <Navbar.Brand href="#home">{this.props.brandName}</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <NavDropdown title={<UserImageComponent profilepicture={this.props.loginUserDetails.profilepicture} profileName={this.props.loginUserDetails.name} profileUsername={this.props.loginUserDetails.username}/>} id="basic-nav-dropdown">
                                <div className="dropdown-user py-2 px-3 text-center">
                                    <img className="rounded-circle text-center mb-1" src={this.props.loginUserDetails.profilepicture} height={60} width={60} alt={this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username} />
                                    <p className="mb-2">{this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username}</p>
                                    <p className="m-0 text-muted">{this.props.loginUserDetails.email}</p>
                                </div>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.2">
                                    <img className="rounded-circle text-center" src={this.props.loginUserDetails.profilepicture} height={30} width={30} alt={this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username} />
                                    &nbsp; <span className="mb-2">{this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username}</span>
                                </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.3">
                                    <img className="rounded-circle text-center" src={this.props.loginUserDetails.profilepicture} height={30} width={30} alt={this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username} />
                                    &nbsp; <span className="mb-2">{this.props.loginUserDetails.name ? this.props.loginUserDetails.name : this.props.loginUserDetails.username}</span>
                                </NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="/">
                                    <div className="text-center">
                                        <button className="btn btn-danger btn-sign-out px-4">Sign Out</button>   
                                    </div> 
                                </NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        )
    }
}

export default UpperNavigation;