import React, { Fragment } from 'react';

const TabHeading = ({ customClass, heading, activeTabName, handleActiveName}) => {
    
    return (
        <Fragment>
            <ul className={`${customClass && customClass} tab-heading`}>
                {   heading.length > 0 && heading.map((data, index) => (
                        <li 
                            key={index}
                            className={activeTabName === data ? 'active' : ''}
                            onClick={() => handleActiveName(data)}>{data}</li>
                    ))
                }
            </ul>
        </Fragment>
    )
}
export { TabHeading }