import React from 'react';
import actor from '../../assets/images/actor.jpg';

const UserImageComponent = (props) => {
    return(
        <div className="d-inline-block">
            <img className="rounded-circle mx-3 mt-2" src={props.profilepicture ? props.profilepicture : actor} height={40} width={40} alt={props.profileName ? props.profileName : props.profileUsername} />
            <span className="vertical-align-sub">{props.profileName ? props.profileName : props.profileUsername}</span>
        </div>
    )
}
export default UserImageComponent;