import React, { Fragment } from 'react';
import { Card }  from 'react-bootstrap';
import actor from '../assets/images/actor.jpg';
import { listAccounts } from '../apiData';

class Accounts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userList: "",
            isLoading: true,
            noResultFound: false
        }
    }

    handleListOfAccounts = async () => {
        const responseData = await listAccounts();
        console.log(responseData);
        if (responseData.data && responseData.data.hasOwnProperty('users') && responseData.data.users.length > 0) {
            this.setState({
                isLoading: false,
                userList: responseData.data.users
            });
        } else {
            this.setState({
                isLoading: false,
                noResultFound: true
            });
        }

    }

    redirectionProfile = (userData) => {
        console.log(userData);
        if(sessionStorage.getItem('userObject')){
            sessionStorage.removeItem('userObject');
        }
        sessionStorage.setItem("userObject", JSON.stringify(userData));
        window.location.href = "/profile";
    }
    
    componentDidMount() {
        this.handleListOfAccounts();
    }
    render() {
        return (
            <div className="row vh-100 bg-light">
                <div className="col-sm-5 my-auto mx-auto">
                    <Card className="shadow-lg borderRadiusall borderNone">
                        <h4 className="text-center p-5 bg-light borderRadiusTLR m-0">Select an Account</h4>
                        {
                            this.state.isLoading ?
                                <div>Loading.......</div>
                            :
                                <Fragment>
                                    {
                                        this.state.userList.length > 0 ?
                                            <div className={`${this.state.userList.length > 0 ? 'accounts-container-with-scroll' : 'accounts-container-without-scroll'}`}>
                                                {this.state.userList.map((data, index) => (
                                                        <Fragment key={index}>
                                                            <div className="d-flex flex-row bd-highlight p6" onClick={() => this.redirectionProfile(data)}>
                                                                <img className="rounded-circle mx-3" src={data.profilepicture ? data.profilepicture : actor} height={40} width={40} alt={data.name ? data.name : data.username} />
                                                                <div className="bd-highlight lh40">
                                                                    {   data.name ?
                                                                            <span>{data.name}</span>
                                                                        :
                                                                            <span>{data.username}</span>
                                                                    }
                                                                </div>
                                                            </div>
                                                            <hr className="my-1"></hr>
                                                        </Fragment>
                                                    ))
                                                }
                                            </div>
                                        :
                                            <div>No result found</div>
                                    }
                                </Fragment>
                        }
                    </Card>
                </div>
            </div>
        );
    }
}

export default Accounts;