import React, { Component } from 'react';
import { TabContainer, TabContent, TabHeading } from '../Components/Tabs/Tab';
import ProfileComponent from './ProfileComponent';
import PostComponent from './PostComponent';
import GalleryComponent from './GalleryComponent';
import TodoComponent from './TodoComponent';
import UpperNavigation from '../Components/Navigation/UpperNavigation';

class MainScreenWrapper extends Component{
    constructor(props){
        super(props);
        this.state = {
            heading: ["Profile", "Posts", "Gallery", "TODO"],
            activeTabName: "Profile",
            profileObject: ""
        }
    }

    handleActiveName = (tabName) => {
        this.setState({ activeTabName: tabName })
    }

    handleProfileForSpecficUserData = () => {
        if (sessionStorage && sessionStorage.getItem('userObject')) {
            let userObject = JSON.parse(sessionStorage.getItem('userObject'));
            this.setState({ profileObject: userObject });
        }

    }

    componentDidMount() {
        this.handleProfileForSpecficUserData()
    }
    
    render(){
        let activeTab;
        switch(this.state.activeTabName){
            case 'Profile':
                activeTab = <ProfileComponent
                                profileObject={this.state.profileObject}
                            />;
                break;
            case 'Posts':
                activeTab = <PostComponent/>;
                break;
            case 'Gallery':
                activeTab = <GalleryComponent/>;
                break;
            case 'TODO':
                activeTab = <TodoComponent/>;
                break;
            default:
                activeTab =  null;
        }
        return (
            <div className="container-fluid">
                <TabContainer>
                    <div className="row">
                        <div className="col-3 bg-blue-gradient">
                            <div className="d-flex justify-content-center">
                                <TabHeading
                                    customClass="d-flex justify-content-center flex-column align-items-center vh-100"
                                    heading={this.state.heading}
                                    activeTabName={this.state.activeTabName}
                                    handleActiveName={this.handleActiveName}
                                />
                            </div>
                        </div>
                        <div className="col-9">
                            <UpperNavigation loginUserDetails={this.state.profileObject} brandName={this.state.activeTabName}/>
                            <TabContent>
                                <div className="tab-content-area">
                                    {activeTab}
                                </div>
                            </TabContent>
                        </div>
                    </div>
                </TabContainer>
            </div>
        )
    }
}
export default MainScreenWrapper